<template>
  <div class="card">
    <div class="card-header">Product List</div>

    <div class="card-body">
      <p v-if="!productList.length">There is no product in this list.</p>

      <ul v-else class="list-group">
        <li
          v-for="(product, index) in productList"
          :key="index"
          v-on:click="removeProduct(product.id)"
          class="list-group-item"
        >
          <h6 class="font-weight-bold">{{ product.title }}</h6>
          <span>Rp. {{ toCurrency(product.price) }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  // Value yang berasal dari parent.

  props: {
    productList: {
      type: Array,
      require: true,
    },
    removeProduct: {
      type: Function,
      require: true,
    },
  },
  setup() {
    // Return sebuah value yang sudah diformat dengan format keuangan.

    const toCurrency = (value) => {
      return value.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1.");
    };

    return {
      toCurrency,
    };
  },
};
</script>

<style scoped>
.list-group-item:hover {
  background-color: #007bff;
  color: white;
  cursor: pointer;
}
</style>
