<template>
  <div class="card">
    <div class="card-header">Product Form</div>

    <div class="card-body">
      <form v-on:submit.prevent="submitForm">
        <div class="form-group">
          <label for="title">Product Title</label>
          <input
            v-model="product.title"
            type="text"
            class="form-control"
            id="title"
            placeholder="Enter your product title"
          />
        </div>

        <div class="form-group">
          <label for="price">Product Price</label>
          <input
            v-model="product.price"
            type="number"
            class="form-control"
            id="price"
            placeholder="Enter your product price"
          />
        </div>

        <button
          v-bind:disabled="!isFormValid"
          type="submit"
          class="btn btn-primary float-right"
        >
          Submit
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { reactive, ref, computed, watchEffect } from "@vue/composition-api";

export default {
  // Value yang berasal dari parent.

  props: {
    addNewProduct: {
      type: Function,
      require: true,
    },
  },
  setup(props) {
    const product = reactive({
      title: "",
      price: "",
    });

    const formSubmitted = ref(false);

    // Konvert value price menjadi float.

    const floatPrice = computed(() => parseFloat(product.price));

    // Validasi apakah title atau price sudah diisi dengan benar.

    const isFormValid = computed(() => {
      if (product.title.trim().length === 0) {
        return false;
      }

      if (isNaN(floatPrice.value) || floatPrice.value <= 0) {
        return false;
      }

      return true;
    });

    // Fungsi untuk meng-submit form.

    const submitForm = () => {
      props.addNewProduct(product.title, product.price);
      formSubmitted.value = true;
    };

    // Hapus value setelah form di-submit.

    watchEffect(() => {
      if (formSubmitted.value) {
        product.title = "";
        product.price = "";

        formSubmitted.value = false;
      }
    });

    return {
      product,
      submitForm,
      isFormValid,
    };
  },
};
</script>
