<template>
  <div id="app" class="m-5">
    <div class="container">
      <div class="product-header mb-3">
        <h4>Product Form App</h4>
        <span>Total Product: {{ totalProductList }}</span>
      </div>

      <div class="row">
        <div class="col-md-12 col-lg-6 mx-auto mb-3">
          <ProductForm v-bind:addNewProduct="addNewProduct" />
        </div>

        <div class="col-md-12 col-lg-6 col-lg-6 mx-auto">
          <ProductList
            v-bind:productList="productList"
            v-bind:removeProduct="removeProduct"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "@vue/composition-api";

import ProductForm from "@/components/ProductForm.vue";
import ProductList from "@/components/ProductList.vue";

export default {
  name: "App",
  setup() {
    const productList = ref([]);

    // Mendapatkan total productList

    const totalProductList = computed(() => productList.value.length);

    // Fungsi untuk menambah produk baru.

    const addNewProduct = (title, price) => {
      productList.value.push({
        id: Math.random(),
        title,
        price,
      });
    };

    // Fungsi untuk menghapus item di productList;

    const removeProduct = (productId) => {
      const filteredProductList = productList.value.filter(
        (product) => product.id !== productId
      );

      productList.value = filteredProductList;
    };

    return {
      productList,
      addNewProduct,
      removeProduct,
      totalProductList,
    };
  },
  components: {
    ProductForm,
    ProductList,
  },
};
</script>

<style scoped>
.container {
  border: 1px solid #b2bec3;
  border-radius: 0.5rem;
  padding: 2rem;
}

.product-header {
  display: flex;
  justify-content: space-between;
  justify-items: center;
  color: #636e72;
  font-weight: bold;
  border-bottom: 1px solid #b2bec3;
}
</style>
